@import url(https://fonts.googleapis.com/css2?family=Teko&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
}

.header{
    background-color: #2C2C2C;
    border-top: 5px solid gold;
    display: flex;
    width: 100vw;
    height: 150px;
}

.header > img{
    display: block;
    width: 180px;
    height: 180px;
    position: absolute;
    left: 10vw;
    top: -10px;
}
.navigation{
    position: absolute;
    right: 10vw;
    width: 30vw;
    font-size: 25px;
    font-family: 'Teko', sans-serif;
    letter-spacing: 2px;
}

ul{
    list-style-type: none;
    padding-top: 30px;
    display: flex;
    justify-content: space-around;
}

ul > li{
    text-align: center;
}

.navigation a{
    display: block;
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: bold;
}

ul li a:hover{
    border-bottom: 2px solid white;
}
.FrontPanel{
    background-color: #cccccc;
    height: 70vh;
    position: relative;
    overflow: hidden;
}

.FrontPanel h4{
    text-transform: uppercase;
    font-size: 40px;
    text-shadow: 0 0 5px grey;
    position: absolute;
    top: 100px;
    left: 20vw;
}

.FrontPanel h1{
    text-transform: uppercase;
    font-size: 100px;
    text-shadow: 0 0 10px grey;
    position: absolute;
    top: 100px;
    left: 15vw;
}

.FrontPanel span{
    font-size: 25px;
    font-weight: bold;
    font-style: italic;
    position: absolute;
    left: 18vw;
    top: 300px;
}

.FrontPanel button{    
    text-transform: uppercase;
    background-color: gold;
    border: none;    
    position: absolute;
    left: 20vw;
    top: 390px;
}

.FrontPanel button a{
    display: block;
    font-size: 18px;
    font-weight: bold;
    text-decoration: none;
    color: black;
    padding: 20px;
}

.FrontPanel img{
    display: block;
    width: 70vw;
    height: 80vh;
    position: absolute;
    left: 35%;
    top: -15px;
}




.footer{
    color: white;
    font-size: 20px;
    font-family: 'Teko', sans-serif;
    background-color: black;
    text-align: center;
    width: 100vw;
    position: absolute;
    bottom: 0;
    padding: 5px 0;
}
.mediabox{
    width: 150px;
    height: 150px;
    background-color: white;
    position: fixed;
    top: 30vh;
    right: -80px;
    padding-top: 10px;
    border-radius: 20%;
}

.mediabox img{
    padding: 0;
    display: flex;
    margin: 8px 13px;
    width: 40px;
    height: 40px;
}

.mediabox img:hover{
    width: 45px;
    height: 45px;
}
